<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import { Clicks } from '../data/Click'
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'cam-do-chinh-la-may-mu-che-phu-ly-tri',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_1289235413_cam-do-chinh-la-may-mu-che-phu-ly-tri.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Cám dỗ chính là mây mù che phủ lý trí - Qùa Tặng Cuộc Sống',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_1289235413_cam-do-chinh-la-may-mu-che-phu-ly-tri.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Cám dỗ chính là mây mù che phủ lý trí' },
      { name: 'Description', content: 'Cho dù dùng cả cuộc đời để có được tất cả tiền bạc của cả thế giới, nhưng tiền bạc của cả thế giới cũng không mua lại được cuộc đời của bạn. Một con cáo phát hiện một chuồng gà, nhưng con cáo đó vì quá mập nên không...' },

      { itemprop: 'name', content: 'Qùa Tặng Cuộc Sống' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Qùa Tặng Cuộc Sống, Qùa Tặng Cuộc Sống' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
    denmodau() {
      Clicks.clickmodau();
    },
    denketthuc() {
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

